.sap-eureka-ui-framework-toast-layer {
  width: 15em;
  position: fixed;
  z-index: 120;
  bottom: 30px;
  left: calc(50% - 7.5em);
  .sap-eureka-ui-framework-toast-instance {
    width: 100%;
    display: block;
    height: 100%;
    text-align: center;
    box-sizing: border-box;
    max-width: 100%;
    word-wrap: break-word;
    text-align: center;
    text-decoration: none;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: pre-line;
    font-size: 0.875rem;
    font-family: "72","72full",Arial,Helvetica,sans-serif;
    font-weight: normal;
    padding: 1rem;
    background: #ffffff;
    color: #32363a;
    box-shadow: 0 0 0.0625rem rgba(0,0,0,0.15), 0 0.625rem 1.875rem 0 rgba(0,0,0,0.3);
    border-radius: 0.25rem;
    transition: opacity 3.3s,transform 3.4s,top 3.4s,-webkit-transform 3.4s;
    .sapUiIcon {
      margin-right: 5px;
      &.error {
        color: #bb0000;
        &::before {
          content: "";
        }
      }
      &.warn {
        color: #e9730c;
        &::before {
          content: "";
        }
      }
      &.info {
        color: #6a6d70;
        &::before {
          content: "";
        }
      }
      &.success {
        color: #107e3e;
        &::before {
          content: "";
        }
      }
    }
  }
}